.App {
  text-align: center;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

h1, p {
  font-family: Lato;
}

.board-controls {
  display: -ms-flexbox;
  display: flex;
  margin: 20px auto;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  -ms-flex-align: center;
      align-items: center;
}

.whole-board {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column wrap;
      flex-flow: column wrap;
  -ms-flex-line-pack: start;
      align-content: flex-start;
}

.controls {

  width:200px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  
}

.controls div {
  
  width:50px;
  text-align: center;
  vertical-align: middle;
  line-height: 50px;
}

.controls div:nth-child(even) {

width:48px;
height: 48px;
cursor: pointer;
}

.myButton {
	-webkit-box-shadow: 3px 4px 0px 0px #8a2a21;
	box-shadow: 3px 4px 0px 0px #8a2a21;
	background:-webkit-gradient(linear, left top, left bottom, color-stop(0.05, #c62d1f), color-stop(1, #f24437));
	background:-webkit-linear-gradient(top, #c62d1f 5%, #f24437 100%);
	background:-o-linear-gradient(top, #c62d1f 5%, #f24437 100%);
	background:-webkit-gradient(linear, left top, left bottom, color-stop(5%, #c62d1f), to(#f24437));
	background:linear-gradient(to bottom, #c62d1f 5%, #f24437 100%);
	filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#c62d1f', endColorstr='#f24437',GradientType=0);
	background-color:#c62d1f;
	border-radius:18px;
	border:1px solid #d02718;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:14px;
	padding:4px 4px;
	text-decoration:none;
	text-shadow:0px 1px 0px #810e05;
}
.myButton:hover {
	background:-webkit-gradient(linear, left top, left bottom, color-stop(0.05, #f24437), color-stop(1, #c62d1f));
	background:-webkit-linear-gradient(top, #f24437 5%, #c62d1f 100%);
	background:-o-linear-gradient(top, #f24437 5%, #c62d1f 100%);
	background:-webkit-gradient(linear, left top, left bottom, color-stop(5%, #f24437), to(#c62d1f));
	background:linear-gradient(to bottom, #f24437 5%, #c62d1f 100%);
	filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#f24437', endColorstr='#c62d1f',GradientType=0);
	background-color:#f24437;
}
.myButton:active {
	position:relative;
	top:1px;
}

.coord-label {
  margin: 20px;
}

.hoover-coord-form {
width: 70%;
margin: 20px auto 20px auto;
background-color: skyblue;
padding: 10px;
border-radius: 10px;
-webkit-box-shadow: 3px 4px 0px 0px #2d80a2;
box-shadow: 3px 4px 0px 0px #2d80a2;
}

.hoover-coord-form input {
  margin: 10px;
}


